/* global graphql */

const _ = require('lodash');
import React from 'react';
import Breadcrumb from '../components/breadcrumb';
import StandingsSelect from '../components/standings-selector';
import { BootstrapTable, TableHeaderColumn} from 'react-bootstrap-table';
import '../../node_modules/react-bootstrap-table/dist/react-bootstrap-table-all.min.css';

class IndexPage extends React.Component {
	constructor(props) {
			super(props);
			this.teamChangeHandler = this.teamChangeHandler.bind(this);
			this.loadData = this.loadData.bind(this);
			this.state = {
				teamsLabels: [],
				teamSelected: "",
				results: []

			}
			this.options = {
				defaultSortName: 'changeSnap',  // default sort column name
				defaultSortOrder: 'desc'  // default sort order
			};
		}
					
		teamChangeHandler = (event) => {
			var selected = event.target.value;
			this.setState(
				{ teamSelected: selected },
				() => this.loadData()
			)
		}

		positionClickHandler = event => {
			this.loadData();

		}
	
		loadData = () => {
			
			var team = this.state.teamSelected;
			var rb = this.refs.rb.checked;
			var wr = this.refs.wr.checked;
			var te = this.refs.te.checked;
			var search = this.refs.search.value;
			const games = this.props.data.allDataJson.edges[0].node.games;
			var results = [];
			var allData = [];
			const playerIds = this.state.playerIds;

			var gamesPlayer;
			_.forEach(playerIds, (id) => {
				gamesPlayer = _.filter(games, ['playerId', id]);
				var snapsPerPlayer = [];
				var tarPerPlayer = [];
				_.forEach(gamesPlayer, (game) => {
					snapsPerPlayer.push(_.get(game, 'offSnapPer', 0));
					tarPerPlayer.push(_.get(game, 'tarPer', 0));
				});
				console.log("snapsPerPlayer",snapsPerPlayer);
				var week1 = _.filter(gamesPlayer, ['week', 'Week 1'])[0];
				var week2 = _.filter(gamesPlayer, ['week', 'Week 2'])[0];
				var week3 = _.filter(gamesPlayer, ['week', 'Week 3'])[0];
				var week4 = _.filter(gamesPlayer, ['week', 'Week 4'])[0];
				var week5 = _.filter(gamesPlayer, ['week', 'Week 5'])[0];
				var week6 = _.filter(gamesPlayer, ['week', 'Week 6'])[0];
				var changeSnap = parseFloat((_.get(week6, 'offSnapPer', 0) - _.get(week5, 'offSnapPer', 0)).toFixed(2));
				var changeTarget = parseFloat((_.get(week6, 'tarPer', 0) - _.get(week5, 'tarPer', 0)).toFixed(2));
				if (_.get(week5, 'offSnapPer', 0) === 0 || _.get(week6, 'offSnapPer', 0) === 0) {
					changeSnap = 0;
					changeTarget = 0;
				}
				allData.push({
					name: gamesPlayer[0].name,
					pos: gamesPlayer[0].pos,
					team: gamesPlayer[0].team,
					week1: `Snaps: ${_.get(week1, 'offSnapPer', 0)}%
Target Share: ${_.defaultTo(_.get(week1, 'tarPer', 0),0)}%
Utilization: ${_.defaultTo(_.get(week1, 'utilization', 0),0)}`,
					week2: `Snaps: ${_.get(week2, 'offSnapPer', 0)}%
Target Share: ${_.defaultTo(_.get(week2, 'tarPer', 0),0)}%
Utilization: ${_.defaultTo(_.get(week2, 'utilization', 0),0)}`,
					week3: `Snaps: ${_.get(week3, 'offSnapPer', 0)}%
Target Share: ${_.defaultTo(_.get(week3, 'tarPer', 0),0)}%
Utilization: ${_.defaultTo(_.get(week3, 'utilization', 0),0)}`,
					week4: `Snaps: ${_.get(week4, 'offSnapPer', 0)}%
Target Share: ${_.defaultTo(_.get(week4, 'tarPer', 0),0)}%
Utilization: ${_.defaultTo(_.get(week4, 'utilization', 0),0)}`,
					week5: `Snaps: ${_.get(week5, 'offSnapPer', 0)}%
Target Share: ${_.defaultTo(_.get(week5, 'tarPer', 0), 0)}%
Utilization: ${_.defaultTo(_.get(week5, 'utilization', 0), 0)}`,
					week6: `Snaps: ${_.get(week6, 'offSnapPer', 0)}%
Target Share: ${_.defaultTo(_.get(week6, 'tarPer', 0), 0)}%
Utilization: ${_.defaultTo(_.get(week6, 'utilization', 0), 0)}`,
					changeSnap: changeSnap,
					changeTarget: changeTarget,
					averageSnap: Number(_.mean(snapsPerPlayer).toFixed(2)),
					averageTarget: Number(_.mean(tarPerPlayer).toFixed(2)),
				});
			});
			
			var filterPos = [];

			if (te) filterPos.push('TE');
			if (wr) filterPos.push('WR');
			if (rb) filterPos.push('RB');

			// Final Results to print in table 
			allData.map((player) => {
				if (_.toLower(player.name).includes(_.toLower(search))) {	
					if (_.some(filterPos, (el) => _.includes(player.pos, el))) {
						if (_.includes(player.team, team) || team === 'All') {
							results.push(player);
						}
					}
				}
			});

			this.setState(
				{ results: results }
			)
		}
	
	UNSAFE_componentWillMount() {
		const games = this.props.data.allDataJson.edges[0].node.games;
		const teamsUnique = _.uniqBy(games, 'team');
		const idUnique = _.uniqBy(games, 'playerId');
		var teamNames = [];
		teamsUnique.map((game) => {
			teamNames.push(game.team);
		});
		var playerIds = [];
		idUnique.map((game) => {
			playerIds.push(game.playerId);
		}); 
		
		teamNames.sort();
		this.setState({ teamNames: teamNames });	
		this.setState({ playerIds: playerIds });
	}        
  	
  	componentDidMount() {
		this.loadData()
	}
		
  	render() {

		return (
			
		   <main className="container-fluid">
			     <div className="card mb-3">
			        <div className="card-header"> 
						<form>
							<div className="form-row align-items-center">
								<div className="col-auto">
									<div className="form-check mb-2">
										<input className="form-check-input" type="checkbox" id="rb" ref="rb" onChange={this.positionClickHandler} defaultChecked={true} />
										<label className="form-check-label" >RB</label>
									</div>
								</div>
								<div className="col-auto">
									<div className="form-check mb-2">
										<input className="form-check-input" type="checkbox" id="wr" ref="wr" onChange={this.positionClickHandler} defaultChecked={true} />
										<label className="form-check-label" >WR</label>
									</div>
								</div>
								<div className="col-auto">
									<div className="form-check mb-2">
										<input className="form-check-input" type="checkbox" id="te" ref="te" onChange={this.positionClickHandler} defaultChecked={true} />
										<label className="form-check-label" >TE</label>
									</div>
								</div>
								<StandingsSelect data={this.state.teamNames} change={(event) => this.teamChangeHandler(event)} />
								<div className="col-sm-4 col-md-3">
									<div className="input-group mb-2">
										<input className="form-control" type="text" id="search" placeholder="Player name" ref="search" onChange={this.positionClickHandler} />
									</div>
								</div>			
							</div>
						</form>
						
					  </div>
			        <div className="card-body">
			          <div className="table-responsive">
						  
							<BootstrapTable data={this.state.results} options={this.options} striped hover version='4'>
								<TableHeaderColumn isKey dataField='name' isKey={true} dataSort={true} tdStyle={{ whiteSpace: 'pre-wrap', verticalAlign: 'middle' }} width="14%" >Name</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ verticalAlign: 'middle', textAlign: "center" }} dataField='pos' dataSort={ true } width="6%">Pos</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ verticalAlign: 'middle', textAlign: "center" }} dataField='team' dataSort={true} width="6%" >Tm</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ whiteSpace: 'pre-wrap', paddingRight: '0' }} thStyle={{ whiteSpace: 'normal' }} dataField='week3' width="14%" >Week 3</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ whiteSpace: 'pre-wrap', paddingRight: '0' }} thStyle={{ whiteSpace: 'normal' }} dataField='week4' width="14%">Week 4</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ whiteSpace: 'pre-wrap', paddingRight: '0' }} thStyle={{ whiteSpace: 'normal' }} dataField='week5' width="14%">Week 5</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ whiteSpace: 'pre-wrap', paddingRight: '0' }} thStyle={{ whiteSpace: 'normal' }} dataField='week6' width="14%">Week 6</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ verticalAlign: 'middle', textAlign: "center" }} dataField='changeSnap' dataSort={true} width="10%" >Snap % &Delta;</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ verticalAlign: 'middle', textAlign: "center" }} dataField='averageSnap' dataSort={true} width="11%" >Snap % Avg</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ verticalAlign: 'middle', textAlign: "center" }} dataField='changeTarget' dataSort={true} width="10%" >Target % &Delta;</TableHeaderColumn>
								<TableHeaderColumn tdStyle={{ verticalAlign: 'middle', textAlign: "center" }} dataField='averageTarget' dataSort={true} width="12%" >Target % Avg</TableHeaderColumn>
						</BootstrapTable>
			          </div> 
			        </div>
			        <div className="card-footer small text-muted"></div>
			      </div> 
		   </main>);  
	}
}
export default IndexPage;
 
export const pageQuery = graphql`
  query standingsQuery {
		allDataJson {
			edges {
				node {
					games {
						week
						name
						pos
						player
						offSnap
						offSnapPer
						team
						cmp
						att
						rush
						tar
						playerId
						tarPer
						rec
						rushYds
						rushTd
						rushLong
						recievingYds
						recievingTds
						recievingLong
						fumbles
						fumbleLost
						utilization
					}
				} 
			}
		}
}
`;



